import React from "react";
import { defineFeature, loadFeature } from "jest-cucumber";
import { Provider } from "react-redux";
import { BrowserRouter, Route, Routes, Navigate } from "react-router-dom";
import { fireEvent, render, waitFor } from "@testing-library/react";
import "@testing-library/jest-dom/extend-expect";
import { store } from "../../Redux/store";
import routes from "./../../utils/Routes";
import Home from "../../Dictionary/Home";
import Details from "../../Dictionary/Details";

const feature = loadFeature("src/Components/Features/home.feature");

defineFeature(feature, (test) => {
  test("The component renders and a random word is fetched", ({
    given,
    when,
    then,
  }) => {
    given("The user is on the Home page", () => {
      const screen = render(
        <Provider store={store}>
          <BrowserRouter>
            <Routes>
              <Route path="/search" element={<Home />} />
              <Route path="/details/:id" element={<Details />} />
              <Route path="*" element={<Navigate to="/search" replace />} />
            </Routes>
          </BrowserRouter>
        </Provider>
      );
      expect(screen.getByTestId("homeContainer")).toBeInTheDocument();
    });
    when("A random word is fetched", async () => {
      const screen = render(
        <Provider store={store}>
          <BrowserRouter>
            <Routes>
              <Route path="/search" element={<Home />} />
              <Route path="/details/:id" element={<Details />} />
              <Route path="*" element={<Navigate to="/search" replace />} />
            </Routes>
          </BrowserRouter>
        </Provider>
      );
      const word = "example";
      const mockAxios = jest.spyOn(require("axios"), "get");
      mockAxios.mockResolvedValueOnce({ data: [word] });
      await screen.findByText(`Word: ${word}`);
      mockAxios.mockRestore();
    });

    then("the definition of the word is fetched immediately", async () => {
      const screen = render(
        <Provider store={store}>
          <BrowserRouter>
            <Routes>
              <Route path="/search" element={<Home />} />
              <Route path="/details/:id" element={<Details />} />
              <Route path="*" element={<Navigate to="/search" replace />} />
            </Routes>
          </BrowserRouter>
        </Provider>
      );
      const mockAxios = jest.spyOn(require("axios"), "get");
      mockAxios.mockResolvedValueOnce({
        data: [
          {
            meanings: [
              {
                definitions: [
                  { definition: "a thing characteristic of its kind" },
                ],
              },
            ],
          },
        ],
      });
      const definition = screen.getByText("a thing characteristic of its kind");
      definition.click();
      await screen.findByText(/part of speech/i);
      mockAxios.mockRestore();
    });
  });
});
